.warpBox {
  width: 100%;
  height: 100%;
}

.warpBox .main_box {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.warpBox .main_box .img_box {
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  background: #ff4794;
  margin: 1rem auto .4rem;
}

.warpBox .main_box .img_box img {
  margin-top: .6rem;
  vertical-align: middle;
}

.warpBox .main_box .msg {
  font-size: .45rem;
  line-height: .8rem;
}

.warpBox .main_box .msg_tit {
  font-size: .5rem;
  line-height: 1.3rem;
  font-weight: 600;
  color: #666;
}

.warpBox .order_box {
  width: 90%;
  margin: .2rem auto 0;
  padding: .2rem 0;
  background: url(../../../../../imgs/payimgs/box.png) no-repeat;
  background-size: 100%;
}

.warpBox .order_box .order {
  width: 100%;
  padding: .3rem;
  display: flex;
  flex-direction: column;
}

.warpBox .order_box .order .tit {
  width: 80%;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
}

.warpBox .order_box .order .tit img {
  height: .1rem;
  width: 25%;
  align-self: center;
}

.warpBox .order_box .order .tit span {
  font-size: .6rem;
  color: #666;
}

.warpBox .order_box .order span {
  font-size: .45rem;
  color: #976666;
  text-align: center;
  line-height: .8rem;
}

.warpBox .order_box .order .goods {
  width: 100%;
  display: flex;
}

.warpBox .order_box .order .goods .left {
  flex: 1 1;
  width: 30%;
}

.warpBox .order_box .order .goods .left img {
  width: 100%;
}

.warpBox .order_box .order .goods .right {
  padding-left: .3rem;
  flex: 2 1;
  display: flex;
  flex-direction: column;
}

.warpBox .order_box .order .goods .right .title {
  font-size: .36rem;
  color: #000;
  line-height: .5rem;
  text-align: left;
}

.warpBox .order_box .order .goods .right .pay_box {
  display: flex;
}

.warpBox .order_box .order .goods .right .pay_box i {
  width: 1.5rem;
  height: .65rem;
  font-size: .35rem;
  line-height: .65rem;
  border-radius: .325rem;
  background: #f9c;
  text-align: center;
  color: #fff;
  font-style: normal;
}

.warpBox .order_box .order .goods .right .pay_box .pay {
  color: #f1486e;
  margin: 0 .35rem;
}

.warpBox .order_box .order .goods .right .pay_box .origin_pay {
  font-size: small;
  text-decoration: line-through;
  color: #ccc;
}

.warpBox .order_box .order .user_icon {
  width: 100%;
  display: flex;
  padding: .5rem;
  margin: .3rem 0 0;
}

.warpBox .order_box .order .user_icon i {
  margin: 0 auto;
}

.warpBox .order_box .order .user_icon i img {
  text-align: center;
}

.warpBox .order_box .order .timer {
  width: 65%;
  background: #1596fe;
  font-size: .35rem;
  color: #fff;
  line-height: .5rem;
  border-radius: .125rem;
  text-align: center;
  margin: 0 auto;
}

.warpBox .order_box .but {
  width: 85%;
  border-radius: .65rem;
  background-color: #ff4794;
  margin: 0 auto;
  color: #fff;
}